<!-- eslint-disable vuejs-accessibility/anchor-has-content -->
<template>
  <section class="py-4">
    <div class="d-flex justify-content-end">
      <button type="button" class="btn btn-dark mb-3" @click="openModal(false)">創建工單</button>
    </div>
    <table class="table table-striped">
      <thead>
        <tr>
          <th scope="col">#</th>
          <th scope="col">狀態</th>
          <th scope="col">廠商</th>
          <th scope="col">工人</th>
          <th scope="col">價錢</th>
          <th scope="col">地址</th>
          <th scope="col">時間</th>
          <th scope="col">工地圖</th>
        </tr>
      </thead>
      <tbody>
        <template v-for="(item, index) in orderList" :key="index">
          <tr>
            <th scope="row">1</th>
            <td>
              <select class="form-select" aria-label="Default select example" style="width: 8rem">
                <option value="未完工">未完工</option>
                <option value="完工">完工</option>
              </select>
            </td>
            <td>
              {{ item.manufact.name }}<br />{{ item.manufact.title }}<br />{{ item.manufact.phone
              }}<br />統編：{{ item.manufact.compiled }}
            </td>
            <td>
              <template v-for="(woker, index) in item.workers" :key="index">
                <button
                  type="button"
                  class="btn me-1"
                  data-bs-toggle="tooltip"
                  data-bs-placement="top"
                  :data-bs-title="`電話：${woker.phone}，工時：${woker.time}`"
                  :class="{'btn-outline-success': woker.status}"
                >
                  {{ woker.name }}
                </button>
              </template>
            </td>
            <td>${{ this.$toCurrency(12324) }}</td>
            <td>386 Windler Drives Apt. 358</td>
            <td>2023/11/27</td>
            <td><img src="https://fakeimg.pl/100/" alt="" /></td>
            <!-- <td>
            <a href="#" @click.prevent="openModal"
              ><i class="bi bi-pencil-square text-success h4"></i
            ></a>
          </td> -->
          </tr>
        </template>
      </tbody>
    </table>
  </section>
  <CreateModel ref="openModal" :product="product" />
</template>

<script>
import orderJson from '@/assets/json/order.json';
import { Tooltip } from 'bootstrap';
import CreateModel from '@/components/CreateModel.vue';

export default {
  data() {
    return {
      isNew: false,
      test: '098765432',
      time: 5,
      tooltipTriggerList: [],
      tooltipList: '',
      product: {},
      orderList: orderJson.orders,
    };
  },
  components: {
    CreateModel,
  },
  methods: {
    getData() {
      this.isLoading = true;
      const api = 'https://ec-course-api.hexschool.io/v2/api/woker_order/admin/products/all';
      this.$http
        .get(api)
        .then((res) => {
          this.workOrder = Object.values(res.data.products);
          this.workOrder.forEach((item) => {
            // eslint-disable-next-line no-param-reassign
            item.date = item.date.substr(0, 10);
          });
          this.isLoading = false;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    openModal(isNew, item) {
      this.isNew = isNew;
      if (this.isNew) {
        this.product = {
          imagesUrl: [],
        };
      } else {
        this.product = { ...item };
      }
      this.$refs.openModal.showModal();
    },
  },
  mounted() {
    this.tooltipTriggerList = document.querySelectorAll('[data-bs-toggle="tooltip"]');
    this.tooltipList = [...this.tooltipTriggerList].map(
      (tooltipTriggerEl) => new Tooltip(tooltipTriggerEl),
    );
  },
};
</script>
