<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <!-- Modal -->
  <div
    class="modal fade"
    data-bs-backdrop="static"
    data-bs-keyboard="false"
    tabindex="-1"
    ref="workModal"
  >
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h1 class="modal-title fs-5">Modal title</h1>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body">
          <!-- 廠商 -->
          <h4>廠商：</h4>
          <div class="mb-3">
            <select
              class="form-select"
              aria-label="Default select example"
              v-model="tempData.title"
            >
              <option disabled selected>選擇廠商</option>
              <template v-for="(item, index) in manufacturers" :key="index">
                <option :value="item">{{ item.name }}</option>
              </template>
            </select>
          </div>
          <!-- 金額 -->
          <h4>金額：</h4>
          <div class="input-group mb-3">
            <span class="input-group-text">$</span>
            <input
              type="text"
              class="form-control"
              v-model="tempData.price"
              aria-label="Amount (to the nearest dollar)"
            />
            <span class="input-group-text">.00</span>
          </div>
          <!-- 工人 -->
          <h4>工人：</h4>
          <section class="container">
            <div class="row row-cols-3 row-cols-md-4 g-3 mb-3">
              <div class="form-check" v-for="(item, index) in workers" :key="index">
                <input
                  class="form-check-input"
                  type="checkbox"
                  :value="item"
                  :id="index"
                  v-model="unit"
                />
                <label class="form-check-label" :for="index">{{ item.name }}</label>
              </div>
            </div>
            <template v-for="(item, index) in unit" :key="index">
              <div class="input-group mb-2">
                <input
                  type="text"
                  placeholder="工人姓名"
                  :value="item.name"
                  class="form-control"
                  disabled
                />
                <input
                  type="text"
                  aria-label="Last name"
                  placeholder="工人電話"
                  class="form-control"
                  :value="item.phone"
                  disabled
                />
                <input
                  type="number"
                  aria-label="Last name"
                  placeholder="工作時數"
                  class="form-control"
                />
              </div>
            </template>
          </section>
          <!-- 工人 -->
          <h4>調用工人：</h4>
          <template v-for="(item, index) in tempWorker" :key="index">
            <div class="input-group mb-2">
              <input type="text" placeholder="工人姓名" class="form-control" />
              <input
                type="text"
                aria-label="Last name"
                placeholder="工人電話"
                class="form-control"
              />
              <input
                type="number"
                aria-label="Last name"
                placeholder="工作時數"
                class="form-control"
              />
              <button
                class="btn btn-outline-danger"
                type="button"
                id="button-add"
                @click="deleteArray(index)"
              >
                刪除
              </button>
            </div>
          </template>
          <div class="d-flex justify-content-center">
            <button class="btn btn-outline-dark" type="button" id="button-on1" @click="addArray">
              新增
            </button>
          </div>
          <!-- 施工地址 -->
          <div class="mb-3">
            <label for="exampleFormControlTextarea1" class="form-label h4">施工地址：</label>
            <textarea
              class="form-control"
              id="exampleFormControlTextarea1"
              rows="3"
              v-model="tempData.address"
            ></textarea>
          </div>
        </div>
        <!-- 施工日期 -->
        <div class="mb-3 container">
          <label for="formControlBirth" class="form-label h4">施工日期：</label>
          <VueDatePicker
            id="formControlBirth"
            format="yyyy-MM-dd"
            :flow="flow"
            :enable-time-picker="false"
            v-model="date"
          >
          </VueDatePicker>
        </div>
        <!-- 工地圖 -->
        <div>
          <div class="container mb-3">
            <label for="img" class="form-label h4">圖片網址：</label>
            <input
              type="email"
              class="form-control"
              id="img"
              placeholder="https://fakeimg.pl/300/"
              v-model="tempData.imageUrl"
            />
          </div>
          <div class="container">
            <img :src="tempData.imageUrl" alt="" />
          </div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
          <button
            type="button"
            class="btn btn-primary"
            :class="{ 'd-none': isLoading }"
            @click="createEdit"
          >
            創建
          </button>
          <div class="spinner-border" :class="{ 'd-none': !isLoading }" role="status">
            <span class="visually-hidden">Loading...</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Modal } from 'bootstrap';
import workJson from '@/assets/json/workers.json';
import manufacturersJson from '@/assets/json/manufacturers.json';
// eslint-disable-next-line import/no-extraneous-dependencies
import VueDatePicker from '@vuepic/vue-datepicker';
// eslint-disable-next-line import/no-extraneous-dependencies
import '@vuepic/vue-datepicker/dist/main.css';

export default {
  data() {
    return {
      tempWorker: [{}],
      unit: [],
      modal: '',
      isLoading: false,
      flow: ['year', 'month', 'calender'],
      tempData: {},
      workers: workJson.workers,
      manufacturers: manufacturersJson.manufacturers,
    };
  },
  props: ['product'],
  watch: {
    product() {
      this.tempData = this.product;
    },
  },
  components: {
    VueDatePicker,
  },
  methods: {
    showModal() {
      this.modal.show();
    },
    hideModal() {
      this.modal.hide();
    },
    createEdit() {
      this.isLoading = true;
      const api = 'https://ec-course-api.hexschool.io/v2/api/woker_order/admin/product';
      this.$http.post(api, { data: this.tempData }).then((res) => {
        if (res.data.success) {
          this.isLoading = false;
          this.hideModal();
          this.$emit('getData');
        }
      });
    },
    addArray() {
      this.tempWorker.push({});
    },
    deleteArray(index) {
      this.tempWorker.splice(index, 1);
    },
  },
  mounted() {
    this.modal = new Modal(this.$refs.workModal);
    this.date = new Date();
  },
};
</script>
